/* ==========================================================================
   TABLES
   ========================================================================== */
.data-list-header {
  @apply py-1.5 pr-2 flex flex-wrap items-center border-b;
}

.data-list-search-input {
  @apply text-sm px-2 py-1 outline-none flex-1;
}

.data-list-footer {
  @apply py-1.5 pr-2 flex items-center border-t;
}

.data-table {
  @apply text-left text-grey outline-none max-w-full block;
  overflow-x: auto;
  width: fit-content;
}

.data-table thead {
  @apply bg-grey-20 text-grey-90 uppercase text-2xs tracking-wide outline-none;
}

.data-table thead tr {
  @apply border-b;
}

.data-table thead th {
  @apply font-medium px-2 py-1.5;
}

.data-table thead th:first-child {
  @apply .rounded-tl;
}

.data-table thead th:last-child {
  @apply .rounded-tr;
}

.data-table tbody {
  outline: none;
}

.data-table tbody tr {
  @apply border-b text-sm;
}

.data-table tbody tr:last-child {
  @apply border-none;
}

.data-table tbody tr:hover {
  @apply bg-grey-10;
}

.data-table tbody tr td:first-child {
  @apply .rounded-bl;
}

.data-table tbody tr td:last-child {
  @apply .rounded-br;
}

.data-table tbody tr th {
  @apply py-1 pr-2;
}

.data-table tbody tr td {
  @apply py-1 px-2;
  word-break: break-all;
}

.data-table .checkbox-column {
  @apply .w-4 .pr-0;
}

.data-table .sortable-column {
  @apply cursor-pointer select-none;
}

.data-table .sortable-column:hover {
  @apply bg-grey-30;
}

.data-table .sortable-column.current-column {
  @apply font-bold;
}

.data-table .sortable-column.current-column svg {
  opacity: 1;
}

.data-table .sortable-column svg {
  transition: all 150ms ease;
  opacity: 0;
}

.data-table .sortable-column svg.asc {
  transform: scaleY(-1);
}

.data-table .sortable-row.draggable-mirror {
  @apply bg-white border-b;
}

@screen md {
  .data-table {
    @apply w-full table;
  }
}

.data-list-header ~ .data-table thead th {
  border-radius: 0;
}

td.table-drag-handle {
  @apply w-3 border-r h-full p-1;
  cursor: grab;
  background: config("colors.grey-20") url("../img/drag-dots.svg") center center no-repeat;
}

td.table-drag-handle:hover {
  @apply .bg-grey-30;
}

table.table-legacy {
  margin-top: 25px;
  margin-bottom: 40px;
  width: 100%;
}

table.table-legacy thead {
  border-top: solid 2px #e0e0e0;
}

table.table-legacy tr {
  border-top: solid 1px #e0e0e0;
  position: relative;
}

table.table-legacy tr.warning td {
  background-color: #fcf8e2;
}

table.table-legacy tbody tr.active td {
  background: #f6f9fc;
}

table.table-legacy .checkbox-col {
  width: 37px;
  padding: 5px 5px;
}

table.table-legacy th {
  color: #8f9ca3;
  font-size: 11px;
  font-weight: bold;
  padding: 12px 5px 12px 5px;
  text-transform: uppercase;
}

table.table-legacy td {
  font-size: 14px;
  padding: 10px 5px;
}

table.table-legacy td.minor {
  color: #8f9ca3;
  font-size: 14px;
}

table.table-legacy td input[type="checkbox"] + label {
  position: relative;
  top: 2px;
  vertical-align: 0;
}

table.table-legacy td span.status {
  border-radius: 7px;
  display: inline-block;
  height: 7px;
  width: 7px;
}

table.table-legacy td span.status-draft {
  background: #8f9ca3;
}

table.table-legacy td span.status-hidden {
  border: solid 1px #8f9ca3;
}

table.table-legacy td span.status-live {
  background: #479967;
}

table.table-legacy .handle {
  @apply select-none;
  cursor: ns-resize;
}

.bordered-table {
  border-spacing: 0;
  border-collapse: separate;
  margin-bottom: 20px;
  margin-top: 0;
  table-layout: fixed;
}

.bordered-table > thead,
.bordered-table > thead > tr > td,
.bordered-table > thead > tr > th,
.bordered-table > tbody > tr > td,
.bordered-table > tbody > tr > th {
  border: none;
}

.bordered-table > thead > tr > th, .bordered-table > tbody > tr > th {
  border-top: solid 1px #e0e0e0;
  font-size: 14px;
  text-transform: none;
}

.bordered-table > thead > tr > th, .bordered-table > thead > tr > td, .bordered-table > tbody > tr > th, .bordered-table > tbody > tr > td {
  border-right: solid 1px #e0e0e0;
  border-bottom: solid 1px #e0e0e0;
}

.bordered-table > thead > tr > th:first-child, .bordered-table > thead > tr > td:first-child, .bordered-table > tbody > tr > th:first-child, .bordered-table > tbody > tr > td:first-child {
  border-left: solid 1px #e0e0e0;
}

.bordered-table > thead tr:first-child th:first-child, .bordered-table > tbody tr:first-child th:first-child {
  border-top-left-radius: 3px;
}

.bordered-table > thead tr:first-child th:last-child, .bordered-table > tbody tr:first-child th:last-child {
  border-top-right-radius: 3px;
}

.bordered-table > thead tr:last-child td:first-child, .bordered-table > tbody tr:last-child td:first-child {
  border-bottom-left-radius: 3px;
}

.bordered-table > thead tr:last-child td:last-child, .bordered-table > tbody tr:last-child td:last-child {
  border-bottom-right-radius: 3px;
}

.table-row-placeholder {
  background: #eef2f6;
}

.delete-column {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 22px;
  color: #777;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  line-height: 1;
  text-align: center;
  height: 22px;
  width: 22px;
  float: right;
  cursor: pointer;
}

.delete-column:hover {
  box-shadow: none;
  color: #555;
}

.column-sortable {
  cursor: pointer;
  position: relative;
}

.column-sortable:before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 2px;
}

.column-sortable:hover:before {
  background: #48606f;
}

.column-sortable.active:before {
  background: #3aa3e3;
}

.column-sortable .icon {
  position: relative;
  float: right;
  top: 6px;
  right: 5px;
}

.column-sortable .icon, .column-sortable .icon:before {
  line-height: 0;
}

.column-status {
  text-align: center;
  width: 80px;
}

.column-actions {
  text-align: right;
  width: 30px;
}

table.control {
  @apply select-none;
}

.flush table.control tr th:first-child {
  padding-left: 30px;
}

/* ==========================================================================
   ALERTS
   ========================================================================== */
.sweet-alert button {
  @apply .text-base .rounded .bg-primary;
}

/* ==========================================================================
   ARRAY FIELDTYPE
   ========================================================================== */
.array-table {
  @apply border rounded border-separate text-sm text-left;
}

.array-table tr:not(:last-child) th, .array-table tr:not(:last-child) td {
  @apply border-b;
}

.array-table th {
  @apply border-r p-1 bg-grey-30;
}

.array-table td {
  @apply p-0 m-0;
}

.array-table tr:first-child th {
  @apply rounded-tl-sm;
}

.array-table tr:first-child td,
.array-table tr:first-child .input-text-minimal {
  @apply rounded-tr-sm;
}

.array-table tr:last-child th {
  @apply rounded-bl-sm;
}

.array-table tr:last-child tr,
.array-table tr:last-child .input-minimal {
  @apply rounded-br-sm;
}

.array-table:focus-within {
  @apply focus-outline;
}

.array-table td:focus-within {
  @apply bg-grey-20;
}

.read-only-field .array-table th {
  @apply text-grey-50;
}

.read-only-field .array-table tr td {
  @apply bg-grey-20;
}

/* ==========================================================================
   ASSETS
   ========================================================================== */
.asset-modal .saving {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 200;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  border-radius: 3px;
}

.asset-modal .saving .inner {
  text-align: center;
  padding: 50px;
  margin: 0 auto;
}

.asset-modal .saving i {
  position: relative;
  top: -1px;
  margin-right: 10px;
}

.drag-notification {
  border: 1px dashed #ccc;
  pointer-events: none;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1049;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.drag-notification div {
  font-size: 24px;
}

.drag-notification .icon {
  font-size: 60px;
  color: #333;
  border: 2px solid #333;
