/* ==========================================================================
   TAGS FIELDTYPE
   ========================================================================== */

// Hide the dropdown. It's silly when it's just "Add [the thing you're typing]"
.tags-fieldtype .selectize-dropdown {
    display: none !important;
}

// Selectize Override
.selectize-control {
    &.multi .selectize-input > div,
    &.multi .selectize-input > div.focus,
    &.multi .selectize-input > div.active {
        @apply bg-blue rounded-sm text-white mr-1 my-sm inline-block;
        padding: 2px 8px;


        &.focus, &.active {
            @apply bg-blue-dark text-white;
        }
    }
}

.selectize-control.plugin-remove_button [data-value] .remove {
    border-color: rgba(255,255,255,.8);
    padding-top: 2px;
}



